body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --main: #03dbb5;
  --sec: #0064a7;
  --danger: #ed1c24;
  --text: #000;
  --main2: #21a78f;
}
.container-bg-gif {
  background-image: url("./images/2.gif");
  background-size: 70vw;
  background-repeat: no-repeat;
  background-position-y: 40%;
  height: 500px;
  width: 80vw;
  margin: 0 auto;
  position: relative;

  max-width: 820px;
}
.trans-text {
  background-color: white;
  color: black;
  font-size: 33vw;
  font-weight: bold;
  margin: 0 auto;
  padding: 10px;
  width: 81vw;

  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -53%);
  mix-blend-mode: screen;
  max-width: 820px;
}
@media screen and (min-width: 1024px) {
  .trans-text {
    font-size: 350px;
  }
  .container-bg-gif {
    top: 5vh;
    background-size: 720px;
  }
  .error-info {
    margin-top: 100px;
  }
}
@media screen and (max-width: 425px) {
  .container-bg-gif {
    background-position-y: 45%;
  }
}
nav {
  position: relative;
  height: 8vh;
  background: #fff;
  z-index: 1;
}
.nav-links {
  display: flex;
  list-style: none;
  width: 50%;

  height: 100%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
}
.nav-links li a {
  display: inline-block;
  color: black;
  text-decoration: none;
  font-size: 19px;
  padding: 5px 10px;
  font-weight: 800;
}

@media screen and (max-width: 768px) {
  .nav-links li a {
    color: white;
  }
  .line {
    width: 30px;
    height: 3px;
    background-color: #ff2800;
    margin: 5px;
  }

  nav {
    position: relative;
  }

  .hamburger {
    position: fixed;
    right: 5%;
    top: 5%;
    transform: translate(-5%, -50%);
    cursor: pointer;
    z-index: 1;
  }

  .nav-links {
    position: fixed;
    background: black;
    height: 100vh;
    width: 100vw;
    flex-direction: column;
    clip-path: circle(10px at 90% -2%);
    -webkit-clip-path: circle(10px at 90% -2%);
    transition: all 1s ease-out;
  }
  .nav-links.open {
    clip-path: circle(1100px at 90% -10%);
    -webkit-clip-path: circle(1100px at 90% -1%);
  }
}
.logo {
  position: absolute;
}

.social-icons {
  font-size: 25px;
}

.social-icons div {
  display: inline-block;
  margin-right: 20px;
}

.social-icons-menu {
  font-size: 35px;
}

.social-icons-menu div {
  display: inline-block;
  width: 33px;
  margin-right: 40px;
  color: white;
}

.pill-1 {
  color: black;
  text-decoration: none;
  font-size: 1.7rem;
  font-weight: 500;
  padding: 15px;
  margin-top: 25px;
  border-radius: 25px;
}
.pill-1:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.1);
  background-image: url(./images/depositphotos_128631938-stock-photo-color-burst-background.jpg);
  background-repeat: repeat-x;
  -webkit-background-clip: text;
  background-clip: text;
}
.error-info {
  text-align: center;
}

.pill-2:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.1);
  background-image: url(./images/pill-2.jpg);
  background-repeat: repeat-x;
  -webkit-background-clip: text;
  background-clip: text;
}

.pill-3:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.1);
  background-image: url(./images/pill-3.jpg);
  background-repeat: repeat-x;
  -webkit-background-clip: text;
  background-clip: text;
}

.pill-4:hover {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.1);
  background-image: url(./images/pill-4.jpg);
  background-repeat: repeat-x;
  -webkit-background-clip: text;
  background-clip: text;
}
.single-article-right {
  box-sizing: border-box;
  max-width: 500px;
}
.single-article-left {
  box-sizing: border-box;
  max-width: 500px;
}
.single-article-container {
  position: absolute;
  display: grid;
  width: 86vw;
  height: 82vh;
  margin: 0 auto;
  background-color: white;
  grid-template-columns: 2fr repeat(2, 6fr);
  grid-template-rows: auto;
  grid-column-gap: 40px;
  margin-top: 4vh;
}
.profile-pic {
  border: none;
  border-radius: 100%;
  width: 90px;
  margin: 10px;
}
.media-container img {
  width: 100%;
  margin-top: 40px;
}
.article-heading {
  width: 55%;
  margin: 80px auto;
  font-weight: 800;
  font-size: 50px;
}
.article-heading-2 {
  width: 90%;
  margin: 75px auto;
  font-weight: 550;
  font-size: 35px;
  color: #ff2800;
}
.meta {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.article-meta {
  display: inline-block;
  margin: 10px auto;
}
.single-article-right {
  display: grid;
  padding: 5px;
  background-color: #eee;
}
.article-text {
  font-size: 1.2rem;
  text-decoration: none;
}
.side-bar {
  background-color: #ff2800;
  margin-left: -40px;
  margin-bottom: -70px;
  z-index: 1;
  color: white;
  font-size: 1.2rem;
  height: 100%;
  width: 160px;
}
.side-bar p {
  transform: rotate(-90deg);
  padding: 30px;

  margin-bottom: 50px;
}
.side-bar a {
  text-decoration: none;
  color: white;
}
.side-bar a:hover {
  text-decoration: none;
  color: black;
}
.showcase {
  position: absolute;
  right: 0%;
  display: grid;
  width: 86vw;
  height: 82vh;
  margin: 0 auto;
  background: white;
  grid-template-columns: 8fr 2fr 5fr 3fr;
  margin-top: 4vh;
  max-width: 1200px;
  max-height: 600px;
}
.hero {
  height: 80%;
  margin: 120px auto 20px;
  padding: 20px;
  max-width: 400px;
}
.contrast {
  background: #181818;
}

.side-bar-2 {
  background: #ff2800;
}

.side-bar-2 a {
  text-decoration: none;
  color: white;
}
.side-bar-2 a:hover {
  text-decoration: none;
  color: black;
}
.img-gallery {
  position: relative;
  width: 100%;
  height: 80%;
  margin: 15% 0 15% -150px;
}

.img-gallery img {
  position: absolute;
  width: 100%;
}
.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 0;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 400ms linear 150ms;
}

.fade-exit {
  opacity: 1;
}
.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 150ms linear;
}

.fd-enter {
  opacity: 0;
}

.fd-enter.fd-enter-active {
  opacity: 1;
  transition: opacity 600ms linear;
}

.fd-exit {
  opacity: 1;
}
.fd-exit.fd-exit-active {
  opacity: 0;
  transition: opacity 600ms linear;
}
.fd-exit-done {
  opacity: 0;
}
.hero {
  position: absolute;
}
@media (min-width: 768px) {
  .side-bar {
    max-width: 80px;
    margin: 0;
    z-index: 0;
  }
  .single-article-left {
    width: 70%;
    margin: 40px auto;
    max-width: 500px;
  }
  .single-article-right {
    margin: 40px auto;
    max-width: 500px;
  }
  .single-article-right img {
    max-width: 85%;
  }
  .article-heading {
    margin: 40px auto;
    width: 80%;
  }
  .meta {
    margin-top: 100px;
  }
  .form-container {
    max-width: 512px;
  }
}
@media (max-width: 767px) {
  .side-bar {
    display: block;
    width: 90%;
    margin: 0 10px 0 -20px;
    height: 100px;
    text-align: center;
    font-size: 16px;
    letter-spacing: 3px;
    overflow: hidden;
    margin-top: 8vh;
    height: 50px;
  }
  .side-bar p + p {
    transform: rotate(0deg);
    padding: 10px;
  }
  .side-bar p:nth-child(1) {
    margin-left: 10px;
  }
  .side-bar p:nth-child(2) {
    display: none;
  }
  .side-bar p {
    display: inline-block;
    margin: 0px 10px;
    padding: 0;
  }
  .single-article-container {
    position: absolute;
    top: 0;
    left: 40px;
    right: 0;
    display: block;
    text-align: center;
  }
  .single-article-right img {
    width: 80%;
    margin: 10px auto;
  }
}
.form-container {
  text-align: center;
  width: 80vw;
  margin: 200px auto;
}

form button {
  background: transparent;
  border: none;
}
@media (max-width: 1200px) {
  .showcase {
    grid-template-columns: 8fr 2fr 5fr;
  }
  .side-bar-2 {
    display: none;
  }
  .img-gallery {
    margin: 15% 0 15% -80px;
  }
  .hero {
    padding-right: 80px;
  }
}
@media (max-width: 650px) {
  .showcase {
    grid-template-columns: 8fr 1fr;
    margin-top: 0;
  }
  .hero {
    margin: -100px auto 0 auto;
  }
  .contrast {
    margin-top: 190px;
  }
  .img-gallery {
    margin: 0px 0px 0px 0px;
  }
  .spacer-2 {
    width: 0;
    height: 0;
  }
}
