.herocontainer {
  position: absolute;
  background-image: url("./images/bnw.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
  background-position: center;
}

.ShowCase {
  color: var(--text);
  width: 40%;
  margin: 0 auto 0 auto;
  text-align: center;
}
#herosub {
  font-size: 2.5rem;
  box-sizing: border-box;
  letter-spacing: 4px;
  padding: 5px 5px;
}
.hr {
  width: 150px;
  height: 5px;
  margin: 0 auto 100px auto;
  background: var(--main);
}
#herotitle {
  font-size: 3.5rem;
  text-transform: uppercase;
}
.ShowCase p {
  font-size: 1.8rem;
  letter-spacing: 3px;
}
.ShowCaseButton {
  display: inline-block;
  margin: 80px 40px 20px 30px;
  width: 200px;
  height: 200px;
  background-color: rgba(3, 219, 181, 0.6);
  color: white;
  font-size: 2rem;
}
.ShowCaseButton svg {
  color: white;
  font-size: 6.5rem;
  margin-top: 10px;
  margin-bottom: 10px;
}
.ShowCaseButton span {
  display: inline-block;
  font-size: 0.8rem;
  margin-top: 30px;
}
.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.navbar ul {
  position: absolute;
  top: 2vh;
  right: 2vh;
  list-style-type: none;
  color: white;
}
.navbar ul li {
  display: inline-block;
  margin: 15px 40px;
  letter-spacing: 4px;
  font-size: 1rem;
}
.navbar ul li a {
  text-decoration: none;
  color: white;
}
.navbar ul li a:hover {
  color: var(--main);
  text-decoration: none;
}
.navbar p {
  font-weight: 900;
  font-size: 2rem;
  color: white;
}
#doneaza {
  background-color: red;
  display: block;
  width: 50%;
  text-align: center;
  font-size: 1.3rem;
}
.cntr {
  position: absolute;
  width: 100vw;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: grid;
  grid-template-areas:
    "n n n"
    "t t t"
    "f f f";
}
.sitform {
  height: 475px;
}
.sitheader {
  grid-area: n;
  background-color: var(--main2);
  color: white;
  font-size: 1.4rem;
  text-align: center;
  text-transform: uppercase;
  height: 80px;
}
.sitheader p {
  margin-top: 15px;
  font-weight: 800;
  letter-spacing: 3px;
}
.sithr {
  background-color: white;
  height: 3px;
  width: 15%;
  margin: -15px auto;
}
.sitmid {
  grid-area: t;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "h t m";
  background-color: white;
  color: var(--main2);
}
.sitmid .heading {
  grid-area: h;
  font-size: 4rem;
  padding: 40px;
  text-transform: uppercase;
  font-weight: 900;
}
.sitmid .sittext1 {
  grid-area: t;
  font-size: 1rem;
  padding: 60px 40px;
  font-weight: 500;
}
.sitmid .sittext2 {
  grid-area: m;
  font-size: 1rem;
  padding: 60px 40px;
  font-weight: 500;
}
.sitfot {
  grid-area: f;
  color: white;
  background-color: var(--main2);
  text-align: center;
}
.sith1 {
  position: relative;
  font-size: 1.5rem;
  background-color: var(--sec);
  padding: 10px 40px;
  width: 20%;
  margin: 20px auto;
  z-index: 1;
}
.select-result {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr) minmax(200px, 300px);
}
#city {
  font-size: 4rem;
}
.select-result button {
  background-color: red;
  border: none;
  color: white;
  min-width: 100%;
}
#butwidget {
  width: 120px;
  margin: 0 auto 30px auto;
}
.select-result .sub {
  font-size: 0.7rem;
  text-align: left;
}
#city-cont {
  margin-top: -30px;
}
.sit-meta {
  margin: 70px 10px;
  z-index: 1;
}
.sit-meta h4 {
  border-left: 2px solid white;
  text-align: left;
  padding-left: 5px;
  font-weight: 560;
}
.sit-meta p {
  text-align: left;
}
.sit-meta .pro {
  font-size: 4rem;
}
.footer-img {
  padding: 0;
  margin: 0;
  float: right;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-10%, -20%);
  width: 600px;
  height: 450px;
  overflow: hidden;
}
.footer-overlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to right,
    rgba(33, 167, 143, 1),
    rgba(33, 167, 143, 0.4)
  );
}
.sitform select {
  position: relative;
  z-index: 2;
  width: 200px;
}

.initiativa-cont {
  display: grid;
  grid-template-areas:
    "h h h"
    "m m m"
    "f f f";
}
.initiativa-head {
  grid-area: h;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: var(--main2);
  color: white;
  height: 100px;
}
.initiativa-head ul {
  display: inline-block;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.initiativa-head ul li {
  display: inline-block;
  margin: 30px 20px 0 20px;
  font-weight: 400;
  font-size: 1.3rem;
}
.initiativa-main {
  grid-area: m;
  display: grid;
  grid-template-columns: 2fr 1fr;
  margin-left: 20px;
}
.initiativa-main img {
  width: 450px;
  margin: 10px 20px;
}
.initiativa-footer {
  grid-area: f;
}
.initiativa-title {
  text-align: right;
  margin-right: 15vw;
}
.initiativa-title svg {
  font-size: 4rem;
  margin-bottom: 20px;
  padding-bottom: 0;
}
.initiativa-text-title {
  display: inline-block;
  text-align: left;
}
.initiativa-text-title h2 {
  background-color: white;
  margin-top: 20px;
  display: inline-block;
  padding: 0 10px;
  color: var(--sec);
}
.initiativa-text-title p {
  background-color: var(--sec);
  display: inline-block;
  padding: 0 20px;
  color: white;
  letter-spacing: 2px;
  font-size: 1.1rem;
}
.dot {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: var(--sec);
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-left: -10px;
  z-index: 1;
}
.line {
  display: inline-block;
  height: 4px;
  width: 110px;
  background-color: var(--sec);
  margin-left: -5px;
  margin-bottom: 4.5px;
}
.initiativa-main h2 {
  margin-top: 20px;
  font-size: 4rem;
  font-weight: 900;
  color: var(--main2);
}
.initiativa-main p {
  font-size: 1.9rem;
  font-weight: 800;
  color: var(--main2);
  margin-top: -10px;
}
.dot:hover {
  background-color: var(--main2);
  cursor: pointer;
}
.info-oras {
  position: absolute;
  display: inline-block;
  color: var(--sec);
  font-size: 2rem;
}

.active {
  display: inline-block;
}
.hidden {
  display: none;
}
.dot-active {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: var(--main2);
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-left: -10px;
  z-index: 1;
}
.initiativa-footer {
  background-image: url("./images/bb014ffe-a153-4f5b-8af0-4a8327c97bf6.1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 500px;
  overflow: hidden;
}
.overlay {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  background-color: rgba(3, 219, 181, 0.67);
  overflow: hidden;
  height: 100%;
  z-index: 2;
}
.footer-text {
  width: 80%;
  margin: 50px 70px 0px 50px;
  padding: 50px;
  color: white;
  font-weight: 600;
}
.footer-form-head {
  border: 2px solid white;
  color: white;
  text-align: center;
  margin: 50px;
  width: 350px;
  padding: 20px;
}
.footer-form-head h2 {
  font-weight: 900;
  text-transform: uppercase;
}
.footer-form-head p {
  font-size: 1.1rem;
}
.footer-form-input input {
  display: block;
  margin: 10px 50px;
  width: 350px;
  color: var(--sec);
  letter-spacing: 2px;
  font-weight: 600;
}
.footer-form-input input::placeholder {
  color: var(--sec);
  letter-spacing: 2px;
  font-weight: 600;
}
